import React, { useState, useEffect } from 'react';
import { n } from './helpers.jsx';
import { BarChart, Bar, XAxis, YAxis, Tooltip, ResponsiveContainer } from 'recharts';
import _ from "lodash";
import Select from 'react-select';


export default function DailyDotByProject(data) {
  let [filter, setFilter] = useState([]);
  let [color, setColor] = useState('#0C3EE3');
  let [type, setType] = useState('Parallel Finance');

  const ref = data.parachain;
  const htvls = data?.data?.stats;

  const dailyDotsData = htvls?.map((entry, index) => (
    _.mapKeys(entry, function(value, key) {
      let keys = key.substring(0,4);
      if (keys === 'date') {
        return keys;
      }else {
        return ref.find(x => x.paraid === keys.toString()).name
      }      
    })
  ))

    const nuevocoso = dailyDotsData.map((entry, index) => (
      Object.fromEntries(
        Object.entries(entry).map(([key, value]) => {
          if(index === 0)  {
            return [key,value]
          } else {
            if(key === 'date') {
              return [key,value]
            }else {
              if((value - parseInt(dailyDotsData[index-1][key])) < 1) {
                return [key, 0]
              }else {
                return [key, (value - parseInt(dailyDotsData[index-1][key]))] 
              }
             
            }
          }
          
        })
      ))
    )

    const buttons2 = Object.entries(dailyDotsData[0]).map(([key, value, index]) => {
      if(key.toString() !== 'date') {
        return {
          label: key,
          color: '#0C3EE3',
          value : filter.map((a) => a.key),
        }
      }
    });

  useEffect(() => {
    setFilter(nuevocoso);
  }, [])

  




   const renderDailyDot = (props) => {
    const { payload } = props;
  
    return (
      <ul className="tvl-tooltip"> 
        {
          
          payload?.map((entry, index) => {
            return (
              <li key={`item-${index}`}>
                 <p><strong>{entry.dataKey}</strong></p>
                <p>Date: {entry.payload.date}</p>
                <p>Raised : {n(entry.payload[entry.dataKey])} DOT</p>
               
              </li>
            )
          })
        }
      </ul>
    );
  }


  return (
    <>
        { buttons2.length !== 0 &&
        <Select
          className="select-dropdown"
          options={buttons2.filter(e => e)} 
          placeholder="Select a Network"
          onChange={(opt) => [setType(opt.label),  setColor(opt.color)]}
        />
      }
      <div className="dailydot">
        <div className="columns dashboard__numbers">          
        </div>
        <ResponsiveContainer className="daily-dots" width="100%" height="100%" >
          <BarChart
            width="100%"
            height={300}
            data={filter}

            margin={{
              right:0,
              top: 5,
              left: 20,
              bottom: 5,
            }}
            >
            <defs>
              <linearGradient id="colorUvb" x1="0" y1="0" x2="0" y2="1">
                <stop offset="5.23%" stopColor="#0C3EE3" stopOpacity={0.9}/>
                <stop offset="57.88%" stopColor="#0C3EE3" stopOpacity={0.95}/>
                <stop offset="97.88%" stopColor="#0C3EE3" stopOpacity={1}/>
              </linearGradient>
            </defs>

            <XAxis dataKey="date" tick={{fontSize: 13}}  />
            <YAxis tick={{fontSize: 13}} orientation="right" tickFormatter={(value) => new Intl.NumberFormat('en', { notation: "compact" , compactDisplay: "short" }).format(value)} minTickGap={20}/>
            <Tooltip content={renderDailyDot}/>

            <Bar dataKey={type} fill={color} stroke={color} radius={[4, 4, 0, 0]} />
          </BarChart>
        </ResponsiveContainer>
      </div>
    </>
  )
}
