import React from 'react';

export default function ProgressBar(data) {
  const w  = data.width,
        b  = data.bar,
        c  = data.bar2,
        d  = ((b*100)+(c*100)),
        dd = (100-((b*100)+(c*100))).toFixed(2);
  return (
    <>
      <div className="meter" style={{width: 800*w }} >
        <span style={{width: b*100 + '%'}} data-tooltip={(b*100).toFixed(2) + '%'}></span>
        <span style={{width: parseInt((c*100)).toFixed(2)+ '%'}} data-tooltip={parseInt((c*100)).toFixed(2)+ '%'}></span>
        <span style={{width: dd + '%'}} data-tooltip={dd + '%'}></span>
      </div>
    </>
  )
}