import React, { useEffect, useState } from 'react';
import {  m  } from '../../data/helpers.jsx';
import { PieChart, Pie, Tooltip, Cell, ResponsiveContainer } from 'recharts';

export default function MarketDominance(data) {

  const [markets    , setMarkets    ] = useState({});
  const baseApi    = "https://auction-service-prod.parallel.fi/crowdloan/marketshare/";
  const share      = data?.share?.share;
  const market     = data?.share?.market; 
  
  
  const getKucoin = async () => {
    const res = await fetch(baseApi+"13LHN6KoMJ2BFh12FeE1Lm1kGGT8V2SV3oH6Ftuk3LyqDUmN");
    const details = await res.json();
    const finalData = Object.entries(details.data)?.reduce(function(sum, current) { return parseFloat(sum + current[1]) }, 0);
    setMarkets((net) => ({
      ...net,
      kucoin: {
        name: "Kucoin",
        value : finalData,
        color : "#23af91",
        market : ((finalData/market)*100).toFixed(2)
      }
    }));
  }
  
  const getBinance = async () => {
    const res = await fetch(baseApi+"1xhXb5ZjsKpN1xcvTNMuThndfb1WP9saLKMpNxofGm64U2X");
    const details = await res.json();
    const finalData = Object.entries(details.data)?.reduce(function(sum, current) { return parseFloat(sum + current[1]) }, 0);
    setMarkets((net) => ({
      ...net,
      binance: {
        name: "Binance",
        value : finalData,
        color : "#f6cc27",
        market : ((finalData/market)*100).toFixed(2)
      }
    }));
  }

  const getKraken = async () => {
    const res = await fetch(baseApi+"1344zprT7A4VFrQ46GJycLrjhBoYjf1RcsjbRLmk5DtsYshV");
    const details = await res.json();
    const finalData = Object.entries(details.data)?.reduce(function(sum, current) { return parseFloat(sum + current[1]) }, 0);
    setMarkets((net) => ({
      ...net,
      kraken: {
        name: "Kraken",
        value : finalData,
        color : "#5841d8",
        market : ((finalData/market)*100).toFixed(2)
      }
    }));
  }
  
  const getBifrost = async () => {
    const res = await fetch(baseApi+"14AMZ3gw4tRsrdp78i4MmHZ8EFbXTMfuXGQMEC3t1GoqLboH");
    const details = await res.json();
    const finalData = Object.entries(details.data)?.reduce(function(sum, current) { return parseFloat(sum + current[1]) }, 0);
    setMarkets((net) => ({
      ...net,
      bifrost: {
        name: "Biforst",
        value : finalData,
        color : "#cb5688",
        market : ((finalData/market)*100).toFixed(2)
      }
    }));
  }

  const getOkex = async () => {
    const res = await fetch(baseApi+"15abPBmJrMY7QJeCEQJQbQ9a62A7ndfTo8KC7Wn4dzt9zMMg");
    const details = await res.json();
    const finalData = Object.entries(details.data)?.reduce(function(sum, current) { return parseFloat(sum + current[1]) }, 0);
    setMarkets((net) => ({
      ...net,
      okex: {
        name: "Okex",
        value : finalData,
        color : "#88bcf2",
        market : ((finalData/market)*100).toFixed(2)
      }
    }));
  }

  const getGate = async () => {
    
    const res = await fetch(baseApi+"13uWt4NnfTuVFxnqTQCryktasmFEFAGpndn19K1ZGxidf7XR");
    const details = await res.json();
    const finalData = Object.entries(details.data)?.reduce(function(sum, current) { return parseFloat(sum + current[1]) }, 0);
    setMarkets((net) => ({
      ...net,
      gate: {
        name: "Gate.io",
        value : finalData,
        color : "#e64e62",
        market : ((finalData/market)*100).toFixed(2)
      }
    }));
  }

  const getEquilibrium = async () => {
    const res = await fetch(baseApi+"13rWYJ2DWj4ZQrZWfVJRJXqVSaQfd34QmRJrsJdSsApnnmK");
    const details = await res.json();
    const finalData = Object.entries(details.data)?.reduce(function(sum, current) { return parseFloat(sum + current[1]) }, 0);
    setMarkets((net) => ({
      ...net,
      equilibrium: {
        name: "Equilibrium",
        value : finalData,
        color : "#1792ff",
        market : ((finalData/market)*100).toFixed(2)
      }
    }));
  }

  const setParallel = () => {
    setMarkets((net) => ({
      ...net,
      parallel: {
        name: "Parallel",
        value : share,
        color : "#0c3ee3",
        market : ((share/market)*100).toFixed(2)
      }
    }));  
  }

  const getPolkadot = () => {
      let global = Object.entries(markets)?.reduce(function(sum, current) { 
        let vals = current[0] === 'others' ? 0 : current[1].value
          
          return parseFloat(sum + vals) 
      }, 0);

    setMarkets((net) => ({
      ...net,
      others: {
        name: "Others",
        value : global,
        color : "#cccccc",
        market : ((global/market)*100).toFixed(2)
      }
    }));     
  }

  useEffect(()=>{
    setParallel();
    getBinance();
    getKraken();
    getBifrost();
    getEquilibrium();
    getOkex();
    getGate();
    getKucoin();
    
  }, [])

  
if(Object.keys(markets)?.length === 8) {
  getPolkadot();
}  

let dataBase2 = Object.values(markets)?.sort((a, b) => b.value - a.value).map((entry, index) => {
  return {
    name: entry.name,
    value : parseFloat(entry.market)
  }
});

let COLORS = Object.values(markets)?.sort((a, b) => b.value - a.value).map((entry, index) => {
  return entry.color
});

  return (  
    <>
    <div className="dominance">
      <div className="chart-dominance">
        <div className="centered score-text">
            {m(data?.share.market)} <small>DOT</small>

        </div>
        <ResponsiveContainer width="100%" height={285}>
        <PieChart >
        <Pie
            data={dataBase2}
            innerRadius={105}
            outerRadius={120}
            cornerRadius={50}
            paddingAngle={1}
            minAngle={8}
            isAnimationActive={false}
            dataKey="value"
          >
            <Tooltip />
            {dataBase2?.map((entry, index) => (
              <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
            ))}
          </Pie>
        </PieChart>  
        </ResponsiveContainer>  
      </div>
      
      { markets &&
      
        Object.values(markets)?.sort((a, b) => b.value - a.value).map((entry, index) => {
          return (
          <div key={index}> 
            <p className="market-paragraph"> 
              <span style={{'background': entry.color}}></span>
              {entry.name} :
              <strong> {m(entry.value)} { '(' + entry.market + '%)'}</strong>
            </p>
          </div>
          )

      })
    }
   
    </div>

    </>
  )
}


