import React, { useEffect, useState } from 'react';
import Header         from './components/header'
import CrowdloanList  from './components/crowdloanList'
import ReactGA from 'react-ga';

import './styles/styles.css'; 

function App() {

  const [raised        , setRaised        ] = useState();
  const [contributions , setContributions ] = useState();
  const [tvl           , setTvl           ] = useState();
  const [DotAmount     , setDotAmount     ] = useState();
  const [historicalTvl , setHistoricalTvl ] = useState();
  const [binance       , setBinance       ] = useState();
  const [dailyContrib  , setDailycontrib  ] = useState();
  const [parachains    , setParachains    ] = useState();

  const getParachains = async () => {
    const res = await fetch("https://cdn.jsdelivr.net/gh/parallel-finance/auction-toolkit@latest/crowdloans/projects/polkadot.json?v2");
    const responseJson = await res.json();
    setParachains(responseJson)
  }

  const getRaised = async () => {
    const res = await fetch("https://auction-service-prod.parallel.fi/crowdloan/polkadot");
    const responseJson = await res.json();
    setRaised(responseJson)
  }

  const dailyContributions = async () => {
    const res = await fetch("https://auction-service-prod.parallel.fi/crowdloan/statistics/polkadot?fundIds=2000-1muqpuFcWvy1Q3tf9Tek882A6ngz46bWPsV6sWiYccnVjKb-0,2002-14e4GmLj5CccWe9Rant9q6yQro1oysqvKiBiHcpCRoscZ1yY-0,2003-1EdsnniYSKNjHNAvDgvBfRNzKnSzi6kgsHQFCG4PhAyyJWH-0,2004-12KHAurRWMFJyxU57S9pQerHsKLCwvWKM1d3dKZVx7gSfkFJ-1,2006-1j5YyEGdcPd9BxkzVNNjKkqdi5f7g3Dd7JMgaGUhsMrZ6dZ-0,2012-15kjdKF4hRbYWzLjovPiohT5pVheXhhk8oKHr3DyTaxF2evd-0,2013-152deMvsN7wxMbSmdApsds6LWNNNGgsJ8TTpZLTD2ipEHNg3-0,2015-1k9St8FDkcTVSe6HzSRQivYi3qgRuQfQp8btpVMrdTFs99S-0,2017-16RzEcgXVzXXn2gEQbqqp74Pw7MJSb7PKtz29BhVZmpXBKRn-0,2018-13n7S6wAFjSqppxyjfSxcPNqQv5jL7PYtxjg3Gxw3RALpyBk-0,2019-12EXcpt1CwnSAF9d7YWrh91bQw6R5wmCpJUXPWi7vn2CZFpJ-0,2021-12LxQoLA9hebiMSPT3G7ixz73LLuYomMNuRLqX7c9bRWxDFG-0,2028-13pQt6LnK2tXZtXbiQ6PBYikEoNTi6MXkeBdQCeyR9hm6k1p-0,2026-16LKv69ct6xDzSiUjuz154vCg62dkyysektHFCeJe85xb6X-0,2008-13QrQ7Xos6bseivYW3xRjvi4T2iHihxVnTrQgyHmWGTNv972-0,2027-14r48SVtMrJKxUWD9ijDy8aQU3asTXja8qny9mzXTutdByju-0,2028-13pQt6LnK2tXZtXbiQ6PBYikEoNTi6MXkeBdQCeyR9hm6k1p-0,2031-143pzStAtMv3RbYbcHyr2xHipWPkme8VjVgAr4QDQP8d3Xrc-0,2032-14gZicKnmFj3238utrQ6B7CGWBeNGntKUyoUHqoTN85FnHWk-0");
    const responseJson = await res.json();
    setDailycontrib(responseJson)
  }

  const getContributions = async () => {
    const res = await fetch("https://auction-service-prod.parallel.fi/crowdloan/polkadot/contributions");
    const responseJson = await res.json();

    setContributions(responseJson)

  }

  const getTvl = async () => {
    const res = await fetch("https://auction-service-prod.parallel.fi/crowdloan/total-value");
    const responseJson = await res.json();
    setTvl(responseJson)
  }



  const getHistoricalTvl = async () => {
    const res = await fetch("https://auction-service-prod.parallel.fi/crowdloan/historical-tvl");
    const responseJson = await res.json();
    setHistoricalTvl(responseJson)
  }

  const getBinance = async () => {
    const res = await fetch("https://www.binance.com/bapi/earn/v1/public/lending/dot/project/home/list?auctioning=true");
    const responseJson = await res.json();
    const res2 = await fetch("https://www.binance.com/bapi/earn/v1/public/lending/dot/project/home/list?auctioning=false");
    const responseJson2 = await res2.json();

    setBinance([responseJson, responseJson2])
  }


  useEffect(()=>{
    ReactGA.initialize('UA-212480889-2');
    ReactGA.pageview('Homepage');

    function load() {
      getParachains();
      dailyContributions();
      getRaised();
      getContributions();
      getTvl();
      getBinance();
      getHistoricalTvl();
      
    }

    load();


      setInterval(() => {
        load()
      }, 30000)
  
  }, [])

  return (
    <>
      <div className="container">
        <Header />
        { raised && parachains && contributions && binance &&
          <CrowdloanList raise={raised} parachain={parachains} contributions={contributions} dailyContrib={dailyContrib} tvl={tvl} htvl={historicalTvl} other={binance}  dotAmount={DotAmount} />
        }
      </div>
    </>
  );
}

export default App;
