import React, { useEffect, useState } from 'react';
import { n, m , Porcent } from '../../data/helpers.jsx';
import GlobalTvl from '../../data/globalTvl.jsx';
import GlobalDotTvl from '../../data/globalDotTvl.jsx';
import Htvl from '../../data/Htvl.jsx';
import Share from '../../data/share.jsx';
import DailyDot from '../../data/DailyDot';
import DailyDotByProject from '../../data/DailyDotByProject';
import Logo from '../../components/Logo';
import ProgressBar from '../../components/ProgressBar';
import Dotmarket from '../../components/Dotmarket';
import MarketDominance from '../../components/MarketDominance'
import DotContributionsFilter from '../../data/DotGroups.jsx';
import _ from 'lodash';
import Select from 'react-select';

export default function CrowdloanList(data) {
  const [AllCrowdloans        , setCrowdloans        ] = useState([]);
  const [showStatus           , setshowStatus        ] = useState("auction611");

  const raising       = data?.raise
  const contrib       = data?.contributions
  const parachains    = data?.parachain;
  const tvl           = data?.tvl;
  const htvl          = data?.htvl;
  const DotAmount     = data?.dotAmount; 
  const DailyContrib  = data?.dailyContrib;
  const marketshare   = [{}];
  const binance       = data?.other;

  let finalBinance = _.merge(_.keyBy(data?.other[0].data, 'id'), _.keyBy(data?.other[1].data, 'id'));
  const market = raising.reduce(function(sum, current) { return parseInt(sum) + parseInt(current.raised); }, 0);
  const share = contrib.reduce(function(sum, current) { return parseInt(sum) + parseInt(current.total); }, 0)
  
  finalBinance = Object.values(finalBinance).filter(c => (c.auctionStatus !== "FAILED"))

  marketshare.market = market;
  marketshare.share  = share;
  marketshare.porcent = Porcent(marketshare.share/marketshare.market);

  useEffect(()=>{
    setCrowdloans(raising.sort((a, b) => b.raised - a.raised).filter(a => (a.status !== "Won")))
  }, [])

  const filterCrowdloan = (props) => {
    switch (props) {

      case "all": 
        setCrowdloans(raising.sort((a, b) => b.raised - a.raised)) 
        setshowStatus("all")
      break
      case "auction15":
        setCrowdloans(raising.sort((a, b) => b.raised - a.raised).filter(fil => (fil.status === "Won")))
        setshowStatus("auction15")
      break;
      case "auction611":
        setCrowdloans(raising.sort((a, b) => b.raised - a.raised).filter(fil => (fil.status !== "Won")))
        setshowStatus("auction611")
      break;
    }
 }
  return (
    <>
        <div className="flex">
          <div className="box tvl half ml-25">
              <h3> Total Value Locked </h3>
              {tvl &&
                <p className="giant">
                  ${ n(tvl.totalValue)}
                </p>
              }
              {htvl && 
                <Htvl data={htvl}/>
              }
              
          </div>

          <div className="box half mr-25" >
            <h3 className="left">Market Share</h3>
            { DailyContrib && htvl &&
            <Share market={marketshare} data={htvl} dailyContrib={DailyContrib}/>
            }
          </div>  
        </div>

        <div className="box full ">
          <h3>Crowdloan by Projects</h3>
          <div className="selectors">
            <p>
              <span><i className="gray"/> Total DOT Raised </span>
              <span><i className="blue"/> Raised on Parallel </span>
              { showStatus === 'auction611' ? '' : <span><i className="yellow"/> Raised on Binance </span> } 
            </p>
            <div className="filterDetails"> 
              <Select
                    
                  options={[{label:"All", value:"all"},{label:"AUCTIONS #1-5", value:"auction15"},{label:"AUCTIONS #6-11", value:"auction611"}]}
                  defaultValue={{ label: "AUCTIONS #6-11", value: "auction611" }}
                  onChange={event => filterCrowdloan(event.value)}
                />
            </div>
          </div>
              
          { !raising && !binance ? 'Loading...' : 
            AllCrowdloans?.map((index) => {
              
              const id = index.paraId,
                    r  = index.raised,
                    t  = raising[0].raised,
                    b  = Object.values(finalBinance).filter(obj => {
                      return obj.code === index.paraId
                    });
                    const dataBinance = b[0] ? b[0].votedAmount*10000000000 : 0;

              return (

                <div  key={id} id={id}>
                  <div className="flex">
                    
                    {!parachains ? 'Loading...' : parachains?.filter(i => i.paraid === id).map(icon => (
                      <Logo key={index.paraId} src={icon}/>
                    ))}    

                    {!contrib ? 'Loading...' : contrib.filter(g => g.paraId === id).map(graph => {
                      const gTotal = graph.total/r,
                            bgTotal = dataBinance/r,
                            rt     = r/t;
                      return (
                      <>
                        <div className="crowdloan-by">
                          <ProgressBar key={index.paraId} width={rt} bar={gTotal} bar2={bgTotal}/>
                          <div>
                            <p className="marketData">
                              <strong> 
                                { r ? m(r) : '0' } 
                              </strong> | <span> 
                                { Porcent(gTotal) + '% (' + m(graph.total) + ')' } 
                              </span>  
                            </p>
                          </div>
                        </div>
                      </>
                    )})}
                    
                  </div>
                </div>
                
              )
          } )
          }
        </div>

        <div className="flex">
          <div className="box small dominance-market">
              <h3>Market Dominance</h3>
              {marketshare &&
                <MarketDominance share={marketshare}/>
              }
          </div> 
          <div className="box tvl auctions" >
            <h3>DOT Auctions 
              <span>Total Supply: <strong>1,103,303,471 DOT</strong></span></h3>
            <Dotmarket raise={raising} parachain={parachains} marketShare={marketshare.market}/>
          </div>  
        </div>

        <div className="box full">
          <h3>Daily Increase of DOT raised during the crowdloan</h3>
          {htvl && 
            <DailyDot contributions={htvl} />
          }
        </div>     
        <div className="box full">
          <h3>Daily Increase of DOT by Project</h3>
          {DailyContrib &&
            <DailyDotByProject parachain={parachains} data={DailyContrib}/>
          }
        </div>               
        <div className="box half ml-25">
          <h3>Crowdloan USD TVL </h3>
          {tvl && DailyContrib &&
            <GlobalTvl price={tvl} dailyContrib={DailyContrib}/>
          }          
        </div>
        <div className="box half mr-25">
          <h3>Crowdloan DOT TVL</h3>
          {tvl && DailyContrib &&
            <GlobalDotTvl price={tvl} dailyContrib={DailyContrib}/>
          }          
        </div>        
        {DotAmount && tvl && 
          <div className="box full distribution">
            <h3>Contribution Distribution</h3>
            <DotContributionsFilter dots={DotAmount} price={tvl}/>
          </div>   
        }
          
    </>
  );
}