import React, { useEffect, useState } from 'react';
import { m, Porcent } from '../../data/helpers.jsx';
import Logo from '../../components/Logo';
import Select from 'react-select';

export default function Dotmarket(data) {
  const raising       = data.raise;
  const parachain     = data.parachain;
  const aucitonShare  = data.marketShare;
  const supply        = '1103303471';

  const [DotCrowdloans        , setDotcrowdloans     ] = useState([]);

  useEffect(()=>{
    setDotcrowdloans(raising.sort((a, b) => b.raised - a.raised).filter(a => (a.status !== "Won")))
  }, []);

  const filteredDots = (props) => {
    switch (props) {

      case "all": 
        setDotcrowdloans(raising.sort((a, b) => b.raised - a.raised)) 
      break
      case "auction15":
        setDotcrowdloans(raising.sort((a, b) => b.raised - a.raised).filter(fil => (fil.status === "Won")))
      break;
      case "auction611":
        setDotcrowdloans(raising.sort((a, b) => b.raised - a.raised).filter(fil => (fil.status !== "Won")))
      break;
    }
 }


  return (
    <>
        <div className="filterDetails"> 
          <Select
                
              options={[{label:"All", value:"all"},{label:"AUCTIONS #1-5", value:"auction15"},{label:"AUCTIONS #6-11", value:"auction611"}]}
              defaultValue={{ label: "AUCTIONS #6-11", value: "auction611" }}
              onChange={event => filteredDots(event.value)}
            />
        </div>
        <div>
          <div className="network-header flex justify">
            <h4 className="network-name">PROJECT</h4>
            <h4 className="network-cap">DOT</h4>
            <h4 className="network-auction">% Auction</h4>
            <h4 className="network-total">% Total</h4>
          </div>
          <div className="scroleable-box">
            { 
              !raising ? "Getting Data" : 
              DotCrowdloans?.map((index) => {
              
              const id = index.paraId,
                    status = index.status,
                    r  = index.raised;
              return (
                <>
                  <div key={id} className="network flex justify">
                    {!parachain ? 'Loading...' : parachain.filter(i => i.paraid === id).map(network => (
                      <>
                        <div  className="network-name"><Logo src={network}/> 
                          <span> 
                            {network.name } 
                            {status === "Won" ? <strong className="winner"> {status} </strong> : ''}
                          </span>
                        </div>
                        <p className="network-cap">{m(r)}</p>
                        <p className="network-auction">{ Porcent(r/aucitonShare) + '%'}</p>
                        <p className="network-total">{ Porcent((r/10000000000)/supply) + '%'}</p>
                      </>
                    ))}    
                    
    
                  </div>
                </>
              )
            })
          }

          </div>
        </div>
    </>
  )
}